import {Carousel} from 'react-bootstrap';
import js from './img/curso-javascript-banner.jpg'
import ht from './img/maquetadoHTML-banner.png'
import './Carrousel.css'

function Carrousel() {
  return (
    <Carousel variant="dark" className='division'>
      <Carousel.Item>
        <img
          className="d-block w-100 tamano"
          src={js}
          alt="First slide"
        />
        <Carousel.Caption className='espacio'>
          <h5 className='h5'>Curso de JavaScript</h5>
          <p className='p'>Dale vida y accin a tu pgina web con estos artculos que te prepararn para ser un desarrollador de JavaScript.</p>
          <h2 className="btn btn-primary btn-lg hero-button" role="button" >Conocer ms</h2>

        </Carousel.Caption >
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100 tamano"
          src={ht}
          alt="Second slide"
        />
        <Carousel.Caption  className='espacio'>
          <h5 className='h5'>Maquetado HTML desde 0</h5>
          <p className='p'>En este artculo te guiaremos para que crees tu primer maquetado en HTML.</p>
          <h2 className="btn btn-primary btn-lg hero-button" role="button" >Conocer ms</h2>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
  );
}

export {Carrousel}